import React from 'react';
import ReactDOM from 'react-dom';
import ApolloClient, { createNetworkInterface } from 'apollo-client';
import { ApolloProvider, gql, graphql } from 'react-apollo';
import './index.css';
// todo remove
// import App from './App';
import registerServiceWorker from './registerServiceWorker';

const networkInterface = createNetworkInterface({
  uri: 'https://api.graph.cool/simple/v1/cj5fnresbjhpa0122howhi004'
})

const client = new ApolloClient({
  networkInterface
})


class TestPage extends React.Component {
	render() {
		console.log(this.props.data)

		if (this.props.data.loading) {
			return (
			<h1> Loading </h1>
			)
		} else {
			return (
				<ul>
					{this.props.data.allFirms.map(firm => (
						<li key={ firm.id }>
							{ firm.name }
						</li>
					))}
				</ul>
			)
		}
	}
}

const FirmQuery = gql`query allFirms {
	allFirms(orderBy: createdAt_DESC) {
		id, name
	}
}`

const FirmListWithData = graphql(FirmQuery, {
	options: {
		fetchPolicy: 'network-only'
	},
})(TestPage)


ReactDOM.render(
	<ApolloProvider client={client}>
		<FirmListWithData />
	</ApolloProvider>
, document.getElementById('root'));
registerServiceWorker();



